<template>
    <div id="login">
        <link href="https://maxcdn.bootstrapcdn.com/font-awesome/4.7.0/css/font-awesome.min.css" rel="stylesheet">
        <div class="container">
            <div class="row login_box">
                <div class="col-md-12 col-xs-12 image-padding" align="center">
                    <div class="outter"><img src="../assets/main.png" class="image-circle" /></div>
                    <h1>Hello User</h1>
                </div>
                
                <div class="col-md-12 col-xs-12 login_control">
                    <div align="center">
                        <button @click="login" class="btn btn-orange"><i class="fa fa-google"></i> Sign In with Google</button>
                        <router-link to="/email" class="btn btn-orange"><i class="fa  fa-envelope"></i> Login with email</router-link>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import firebase from 'firebase'
    export default {
        name: 'login',
        data() {
            return {}
        },
        beforeRouteEnter: (to, from, next) => {
            firebase.auth().onAuthStateChanged((user) =>{
                if (user) {
                    next('/dashboard')
                } else {
                    next()
                }
            })
        },
        methods: {
            goNext() {
                this.$router.push('/dashboard')
            },
            login() {
                let provider = new firebase.auth.GoogleAuthProvider();
                firebase.auth().signInWithPopup(provider).then((result) => {
                    console.log(result)
                    this.goNext()
                }).catch((error) => {
                    console.log(error)
                });
            }
        }
    }
</script>

<style scoped>
    body {
        background-color: #2B2B2B;
    }
    * {
        font-family: 'Raleway', sans-serif; 
        color: #FFF;
    }
    div h3 span {
        color: #FFF;
        font-size: 14px;
    }
    div span {
        font-weight: 200;
    }
    h1 {
        font-weight: 200;
    }
    .login_box {
        background: #673AB7;
        background: url(data:image/svg+xml;base64,);
        background: -moz-linear-gradient(45deg, #673AB7 5%, #E91E63 99%);
        background: -webkit-gradient(linear, left bottom, right top, color-stop(5%, #673AB7), color-stop(99%, #E91E63));
        background: -webkit-linear-gradient(45deg, #673AB7 5%, #E91E63 99%);
        background: -o-linear-gradient(45deg, #673AB7 5%, #E91E63 99%);
        background: -ms-linear-gradient(45deg, #673AB7 5%, #E91E63 99%);
        background: linear-gradient(45deg, #673AB7 5%, #E91E63 99%);
        filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#673AB7', endColorstr='#E91E63', GradientType=1);
        width: 35%;
        position: absolute;
        top: 10%;
        left: 32.5%; 
        -webkit-box-shadow: 0px 0px 8px 0px rgba(50, 50, 50, 0.54);
        -moz-box-shadow: 0px 0px 8px 0px rgba(50, 50, 50, 0.54);
        box-shadow: 0px 0px 8px 0px rgba(50, 50, 50, 0.54);
    }
    @media (max-width: 767px) {
        .login_box {
            background: #673AB7;
            background: url(data:image/svg+xml;base64,);
            background: -moz-linear-gradient(45deg, #673AB7 5%, #E91E63 99%);
            background: -webkit-gradient(linear, left bottom, right top, color-stop(5%, #673AB7), color-stop(99%, #E91E63));
            background: -webkit-linear-gradient(45deg, #673AB7 5%, #E91E63 99%);
            background: -o-linear-gradient(45deg, #673AB7 5%, #E91E63 99%);
            background: -ms-linear-gradient(45deg, #673AB7 5%, #E91E63 99%);
            background: linear-gradient(45deg, #673AB7 5%, #E91E63 99%);
            filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#673AB7', endColorstr='#E91E63', GradientType=1);
            width: 90%;
            height: 80%;
            position: absolute;
            top: 10%;
            left: 5%;
            -webkit-box-shadow: 0px 0px 8px 0px rgba(50, 50, 50, 0.54);
            -moz-box-shadow: 0px 0px 8px 0px rgba(50, 50, 50, 0.54);
            box-shadow: 0px 0px 8px 0px rgba(50, 50, 50, 0.54);
        }
    }
    .image-circle {
        border-radius: 50%;
        width: 100px;
        height: 100px;
        border: 4px solid #FFF;
        margin: 10px;
    }
    .follow {
        background-color: #673AB7;
        height: 80px;
        cursor: pointer;
    }
    .follow:hover {
        background-color: rgb(111, 57, 204);
        height: 80px;
        cursor: pointer;
    }
    .login_control {
        background-color: #FFF;
        padding: 10px;
    }
    .control {
        color: #000;
        margin: 10px;
    }
    .label {
        color: #673AB7;
        font-size: 18px;
        font-weight: 500;
    }
    .form-control {
        color: #000000 !important;
        font-size: 25px;
        border: none;
        padding: 25px;
        padding-left: 10px;
        border-bottom: 1px solid #CCC;
        margin-bottom: 10px;
        outline: none;
        -webkit-box-shadow: none !important;
        -moz-box-shadow: none !important;
        box-shadow: none !important;
    }
    .form-control:focus {
        border-radius: 0px;
        border-bottom: 1px solid #FC563B;
        margin-bottom: 10px;
        outline: none;
        -webkit-box-shadow: none !important;
        -moz-box-shadow: none !important;
        box-shadow: none !important;
    }
    .btn-orange {
        height: auto;
        background: -moz-linear-gradient(45deg, #673AB7 5%, #E91E63 99%);
        background: -webkit-gradient(linear, left bottom, right top, color-stop(5%, #673AB7), color-stop(99%, #E91E63));
        background: -webkit-linear-gradient(45deg, #673AB7 5%, #E91E63 99%);
        background: -o-linear-gradient(45deg, #673AB7 5%, #E91E63 99%);
        background: -ms-linear-gradient(45deg, #673AB7 5%, #E91E63 99%);
        background: linear-gradient(45deg, #673AB7 5%, #E91E63 99%);
        border-radius: 0px;
        margin: 15px;
        padding: 15px;
        font-size: 20px;
        font-weight: inherit;
    }
    .btn-orange:hover {
        background-color: rgb(119, 61, 219);
        border-radius: 0px;
        margin: 5px;
        padding: 10px;
        font-size: 20px;
        font-weight: inherit;
        color: #FFF !important;
    }
    .line {
        border-bottom: 2px solid #673AB7;
    }
    .outter {
        padding: 0px;
        border: 1px solid rgba(255, 255, 255, 0.29);
        border-radius: 50%;
        width: 125px;
        height: 125px;
    }
    .image-padding {
        padding: 15px;
    }
</style>


